.photo-catalog__wrapper {
  padding: 30px 20px 42px;
}

.photo__title {
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  text-align: center;
  text-transform: uppercase;
  margin: 0;
  margin-bottom: 35px;
}

.photo__description {
  text-align: center;
  text-transform: uppercase;
  color: $grey-color;
  margin: 0;
}

.photo__list {
  @include list-reset;
}

.photo__image {
  display: block;
}

.photo__item-wrapper {
  display: flex;
  flex-direction: column;
  padding: 25px 20px;
}

.photo__subtitle {
  font-size: 14px;
  text-transform: uppercase;
  margin: 0;
  margin-top: 15px;
}

.photo__text {
  display: block;
  color: $grey-color;
  margin-bottom: 11px;
}

.photo__number-wrapper {
  display: flex;
  gap: 14px;
  min-height: 26px;
}

.photo__number {
  align-self: flex-end;
  font-weight: 700;
  text-transform: uppercase;
}

.photo__icon {
  fill: #000000;
  opacity: 0.3;
}

// tablet
@media (min-width: $tablet-width) {
  br {
    display: none;
  }

  .photo-catalog__wrapper {
    padding: 66px 165px 67px;
  }

  .photo__title {
    font-size: 30px;
  }

  .photo__list {
    display: grid;
    grid-template-columns: repeat(2, 324px);
    padding: 0 60px;
  }

  .photo__item-wrapper {
    padding: 0;
  }

  .photo__text-number-wrapper {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    background-color: $personal-data-color;
    padding: 22px 20px 15px;
  }

  .photo__subtitle {
    margin: 0;
  }

  .photo__number-wrapper {
    align-items: flex-end;
  }

  .photo__item--open-spaces {
    grid-column: 1/-1;
  }

  .photo__image {
    width: 100%;
    height: auto;
  }


  .photo__image--open-spaces {
    width: 100%;
    height: 400px;
  }

  .photo__icon {
    fill: #000000;
    opacity: 0.3;
  }
}

// desktop
@media (min-width: $desktop-width) {
  .photo__wrapper {
    padding: 0;
  }

  .photo-catalog__wrapper {
    padding: 99px 371px 64px;
  }

  .photo__list {
    display: grid;
    grid-template-columns: repeat(3, 400px);
    padding: 0;
  }

  .photo__item-wrapper {
    position: relative;
    padding: 0;
  }

  .photo__text-number-wrapper {
    position: absolute;
    bottom: 0;
    width: 100%;
    box-sizing: border-box;
    background-color: transparent;
    padding: 22px 20px 15px;
  }

  .photo__subtitle,
  .photo__text,
  .photo__number {
    color: $white-color;
  }

  .photo__icon {
    fill: $white-color;
  }
}
